// *** begin moving-line *** //
.moving-line {
   opacity: 0;
   pointer-events: none;
   position: absolute;
   bottom: 0;
   left: 0;
   width: 100%;
   height: 3px;
   background-color: $color1;
   transition: .3s ease-in-out; }
// end moving-line //

// *** begin socials *** //
.socials {
   display: flex;
   align-items: center;
   a {
      display: flex;
      align-items: center;
      margin-left: 17px;
      &:hover {
         svg {
            fill: $color1; } }
      svg {
         fill: #acafbd;
         transition: .3s ease-in-out; } } }
// end socials //

// *** begin subscribe *** //
.subscribe {
   &__form {
      display: flex;
      margin: 0 -1px;
      &-field {
         flex: 1 1 50%;
         width: 50%;
         padding: 0 1px; }
      &-submit-btn {
         width: 100%;
         padding: 11px 15px 12px;
         color: $color1;
         @include f('pt400', 15);
         border: 2px solid $color1;
         text-align: center; } } }
// end subscribe //

// *** begin control-panel *** //
.control-panel {
   $parent: &;
   display: flex;
   &_vertical {
      position: absolute;
      top: 0;
      flex-direction: column;
      &-r {
         left: 100%; }
      &-l {
         right: 100%; } }
   &__btn {
      position: relative;
      @include flexCenter;
      flex: 0 0 45px;
      width: 45px;
      height: 45px;
      &-tip {
         position: absolute;
         z-index: 1;
         top: 50%;
         opacity: 0;
         display: flex;
         align-items: center;
         min-height: 45px;
         min-width: 95px;
         padding: 7px 5px 8px 12px;
         background-color: #f6f6f6;
         color: $color1;
         font-size: 13px;
         text-align: left;
         transition: .2s ease-in-out;
         pointer-events: none;
         #{$parent}_vertical-l & {
            right: 100%;
            transform: translate3d(-15px, -50%, 0);
            &::before {
               left: 100%;
               @include arr(6, 16, #f6f6f6, r); } }

         #{$parent}_vertical-r & {
            left: 100%;
            transform: translate3d(15px, -50%, 0);
            &::before {
               right: 100%;
               @include arr(6, 16, #f6f6f6, l); } }

         #{$parent}_vertical-r #{$parent}__btn:hover & {
            transform: translate3d(0, -50%, 0);
            opacity: 1; }
         #{$parent}_vertical-l #{$parent}__btn:hover & {
            transform: translate3d(0, -50%, 0);
            opacity: 1; }

         &::before {
            position: absolute;
            top: calc(50% - 8px);
            content: ''; } }
      &_view {}
      &_more {
         background-color: #393f4d; }
      &_bid {
         background-color: #80838f; }
      &_add {
         background-color: #b7bac7; }
      svg {
         fill: #e7e8ec; }
      .icon-view {
         fill: $color1; }
      .icon-list {
         font-size: 14px; }
      .icon-hammer {
         font-size: 21px; }
      .icon-beg {
         font-size: 18px; } } }
// end control-panel //

// *** begin dropdown *** //
.dropdown {
   $parent: &;
   position: relative;
   text-align: center;
   cursor: pointer;
   > ul {
      position: relative;
      cursor: pointer;
      &:hover ul {
         pointer-events: auto;
         opacity: 1;
         transform: scaleY(1); }
      &:hover #{$parent}__icon {
         animation: smallMoveDown .5s ease; }
      > li > a {
         position: relative;
         z-index: 1;
         pointer-events: none;
         span {
            color: $color1; } }
      li {
         &:not(:last-child) {
            margin-bottom: 5px; }
         a {
            display: flex;
            align-items: center;
            cursor: pointer;
            color: $color7;
            white-space: nowrap;
            transition: .3s ease-in-out;
            &:hover {
               color: $color1; } } }
      ul {
         pointer-events: none;
         position: absolute;
         top: -15px;
         left: -33px;
         right: 0;
         transform-origin: 0 0;
         opacity: 0;
         transform: scaleY(0);
         display: table;
         padding: 40px 15px 10px 33px;
         min-width: 130px;
         background-color: #f6f6f6;
         transition: .3s ease-in-out; } }

   &__icon {
      margin: 3px 0 0 6px;
      font-size: 9px; } }
// end dropdown //

.breadcrumbs {
   margin-bottom: 55px;
   ul {
      font-size: 0;
      li {
         display: inline-block;
         vertical-align: middle;
         line-height: 22px;
         &:not(:last-child) {
            margin-right: 10px; }
         &:not(:last-child)::after {
            display: inline-block;
            vertical-align: middle;
            margin-left: 8px;
            pointer-events: none;
            font-size: 15px;
            content: '/'; }
         &.is-active a {
            pointer-events: none;
            color: $color1; }
         a {
            display: inline-block;
            vertical-align: middle;
            font-size: 15px;
            color: $color7;
            @include trs;
            &:hover {
               color: $color1; } } } } }

.pagination {
   text-align: center;
   &_default {
      margin: 70px 0 0;
      @include sm {
         margin: 50px 0 0; } }
   &_catalog {
      margin: 90px 0 0;
      @include sm {
         margin: 60px 0 0; }
      @include xs {
         margin: 40px 0 0; } }
   ul {
      margin-bottom: 13px;
      font-size: 0;
      line-height: 0;
      li {
         display: inline-block;
         vertical-align: middle;
         position: relative;
         margin: 0 12px;
         &:first-child {
            margin-right: 17px; }
         &:last-child {
            margin-left: 17px; }
         @include xs {
            margin: 0 7px; }
         &.is-active {
            &::after {
               @include vcenter;
               z-index: -1;
               width: 35px;
               height: 35px;
               margin-top: -2px;
               background-color: #ececec;
               border-radius: 50%;
               content: ''; }
            a {
               pointer-events: none; } }

         a {
            font-weight: 400;
            text-align: center;
            font-size: 16px;
            color: #181818;
            @include trs;
            &:hover {
               color: rgba(#181818, .5);
               .icon {
                  fill: rgba(#181818, .5); } }
            .icon {
               vertical-align: 4px;
               font-size: 12px;
               @include trs; } } } } }
