.page {
   padding: 53px 0;
   &__container {
      display: flex;
      align-items: flex-start; }
   &__side {
      flex: 0 0 270px;
      width: 270px;
      &-block {
         padding: 25px;
         border: 1px solid #e2e1df;
         &:not(:last-child) {
            margin-bottom: 30px; } }

      &-checkbox-group {
         &:not(:last-child) {
            margin-bottom: 10px; } } }

   &__content {
      flex: 1 1 auto;
      padding-left: 40px; } }

// *** begin checkbox-group *** //
.checkbox-group {
   $parent: &;
   &__check {
      &:not(#{$parent}__check_all) {
         padding-left: 10px; } } }
// end checkbox-group //
