body {
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "PTSans-Regular", Arial, sans-serif;
  font-weight: normal;
  font-size: 15px;
  line-height: 1.2;
  color: #393f4d;
}

body.is-locked {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 0;
  overflow: hidden;
}

.container {
  position: relative;
  max-width: 1246px;
  margin: 0 auto;
}

.container_md {
  max-width: 1028px;
}

.row {
  display: flex;
  margin: 0 -15px;
}

.col {
  padding: 0 15px;
}

.main {
  display: block;
  overflow: hidden;
  padding-top: 60px;
}

.title {
  line-height: 1.2;
  color: #393f4d;
  font-family: "PTSans-Bold", Arial, sans-serif;
  font-weight: normal;
}

.title_lg {
  font-size: 50px;
}

.title_md {
  font-size: 42px;
}

.title_sm {
  font-size: 33px;
}

.title_xs {
  font-size: 21px;
}

.title_gray {
  color: #acafbd;
}

.title_white {
  color: #fff;
}

.btn {
  position: relative;
  display: inline-block;
  padding: 16px 47px;
  font-family: Arial, sans-serif;
  font-size: 18px;
  line-height: 28px;
  text-align: center;
  transition: .3s ease-in-out;
  overflow: hidden;
  background-color: #80838f;
  color: #393f4d;
  border-radius: 30px;
  outline: 0;
  cursor: pointer;
}

.link {
  display: inline-flex;
  align-items: center;
  color: #393f4d;
}

.link_bold {
  font-family: "PTSans-Bold", Arial, sans-serif;
  font-weight: normal;
}

.link_white {
  color: #fff;
}

.link_decor {
  display: inline;
}

.link_decor span {
  border-bottom: 1px solid currentColor;
  transition: .3s ease-in-out;
}

.link_decor:hover span {
  border-bottom-color: transparent;
}

.link__icon {
  margin-left: 7px;
}

.link:hover .link__icon {
  animation: smMoveRight .3s ease-in-out;
}

.link_bold .link__icon {
  margin-left: 11px;
}

.link_white .link__icon {
  fill: #fff;
}

.help-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  background-color: #6600cc;
  transition: .3s ease-in-out;
}

.help-btn:hover {
  background-color: #4d0099;
}

.help-btn svg {
  fill: #fff;
  font-size: 30px;
}

.help-btn__text {
  position: absolute;
  left: calc(100% + 55px);
  width: 80px;
  font-family: "PTSans-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 21px;
  color: #80838f;
  transition: .3s ease-in-out;
}

.help-btn__text:hover {
  color: #393f4d;
}

.moving-line {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #393f4d;
  transition: .3s ease-in-out;
}

.socials {
  display: flex;
  align-items: center;
}

.socials a {
  display: flex;
  align-items: center;
  margin-left: 17px;
}

.socials a:hover svg {
  fill: #393f4d;
}

.socials a svg {
  fill: #acafbd;
  transition: .3s ease-in-out;
}

.subscribe__form {
  display: flex;
  margin: 0 -1px;
}

.subscribe__form-field {
  flex: 1 1 50%;
  width: 50%;
  padding: 0 1px;
}

.subscribe__form-submit-btn {
  width: 100%;
  padding: 11px 15px 12px;
  color: #393f4d;
  font-family: "PTSans-Regular", Arial, sans-serif;
  font-weight: normal;
  font-size: 15px;
  border: 2px solid #393f4d;
  text-align: center;
}

.control-panel {
  display: flex;
}

.control-panel_vertical {
  position: absolute;
  top: 0;
  flex-direction: column;
}

.control-panel_vertical-r {
  left: 100%;
}

.control-panel_vertical-l {
  right: 100%;
}

.control-panel__btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 45px;
  width: 45px;
  height: 45px;
}

.control-panel__btn-tip {
  position: absolute;
  z-index: 1;
  top: 50%;
  opacity: 0;
  display: flex;
  align-items: center;
  min-height: 45px;
  min-width: 95px;
  padding: 7px 5px 8px 12px;
  background-color: #f6f6f6;
  color: #393f4d;
  font-size: 13px;
  text-align: left;
  transition: .2s ease-in-out;
  pointer-events: none;
}

.control-panel_vertical-l .control-panel__btn-tip {
  right: 100%;
  transform: translate3d(-15px, -50%, 0);
}

.control-panel_vertical-l .control-panel__btn-tip::before {
  left: 100%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 8px 0 8px 6px;
  border-color: transparent transparent transparent #f6f6f6;
}

