.form-field {
   $parent: &;
   position: relative;
   display: block;
   &__input {
      width: 100%;
      height: 46px;
      padding: 0 16px;
      @include f('pt400', 15);
      background-color: $white;
      border-radius: 0;
      border: 2px solid #e4e3e1;
      @include placeholder-color($color7);
      color: $color1;
      transition: .3s ease-in-out;
      @include xs {
         padding: 0 20px 0 37px; }
      &:focus {
         border-color: $color1; } }
   &__textarea {
      $parent: &;
      display: block;
      width: 100%;
      height: 150px;
      padding: 18px 20px 18px 55px;
      border: 2px solid $color4;
      border-radius: 30px;
      background-color: $white;
      @include f('pn400', 20, 28);
      transition: .3s ease-in-out;
      @include placeholder-color($color5);
      color: $color1;
      resize: none;
      @include xs {
         padding: 18px 20px 18px 37px; }
      &:focus {
         border-color: $color1; } } }

.check-box {
   $parent: &;
   &.is-disabled {
      pointer-events: none; }
   &__label {
      display: inline-flex;
      align-items: center;
      cursor: pointer; }

   &__square {
      position: relative;
      width: 15px;
      height: 15px;
      flex: 0 0 15px;
      margin-right: 13px;
      border: 1px solid #acafbd;
      @include xs {
         margin-top: 5px; } }
   &__icon {
      opacity: 0;
      transform: scale(0);
      position: absolute;
      top: 2px;
      left: 2px;
      transition: .3s ease-in-out;
      font-size: 10px;
      fill: $color1;
      #{$parent}_radio & {
         display: none; } }
   &__text {
      @include f('ub400', 14, 24);
      @include xs {
         line-height: 20px; }

      #{$parent}_type-2 & {
         color: $color5; } }
   &__input {
      display: none;
      &:checked ~ #{$parent}__square #{$parent}__icon,
      &:checked ~ #{$parent}__square::after {
         opacity: 1;
         transform: scale(1); } } }


// *** begin range-slider *** //
.range-slider-double {
   &__result {
      display: inline-block;
      height: 20px;
      width: 100px;
      @include f('pt700', 15, 15);
      color: $color1;
      &_from {}
      &_to {} } }

// end range-slider //

.irs {
   height: 20px; }

.irs-bar {
   top: calc(50% - 2px);
   height: 3px;
   background: #6600cc;
   border: 0; }

.irs-line {
   top: calc(50% - 2px);
   height: 3px;
   border: 0;
   border-radius: 0;
   background: #e2e1df; }

.irs-slider {
   top: calc(50% - 11px);
   width: 20px;
   height: 20px;
   background: #6600cc;
   transition: background .3s ease-in-out;
   border: 3px solid $white;
   &.state_hover,
   &:hover {
      background: darken(#6600cc, 12); } }

.irs-max, .irs-min,
.irs-from, .irs-single, .irs-to {
   display: none;
