$icons: (0:0);
$icons: map-merge($icons,('arr-r-long-black': (X: -58px, Y:0px, W: 38px, H: 12px, TW: 96px, TH: 56px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('arr-r-long-white': (X: -58px, Y:-22px, W: 38px, H: 12px, TW: 96px, TH: 56px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('arrow-r-black': (X: 0px, Y:-44px, W: 16px, H: 12px, TW: 96px, TH: 56px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('arrow-r-gray': (X: -26px, Y:-44px, W: 16px, H: 12px, TW: 96px, TH: 56px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('arrow-r-white': (X: -52px, Y:-44px, W: 16px, H: 12px, TW: 96px, TH: 56px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('arrow-r': (X: -78px, Y:-44px, W: 16px, H: 12px, TW: 96px, TH: 56px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('sld-arr-l': (X: 0px, Y:0px, W: 48px, H: 12px, TW: 96px, TH: 56px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('sld-arr-r': (X: 0px, Y:-22px, W: 48px, H: 12px, TW: 96px, TH: 56px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }

