.screen {
   $parent: &;
   position: relative;
   &_full {
      background-color: #edeff1;
      height: 492px; }
   &_small {
      padding: 30px 0;
      background-color: #ffe987; }
   &__container {
      position: static;
      display: flex;
      #{$parent}_small & {
         justify-content: space-between;
         padding-right: 60px; } }
   &__content {
      flex: 0 0 500px;
      padding: 50px 0 65px; }
   &__logo {
      display: block;
      width: 198px;
      margin-bottom: 57px; }
   &__title {
      margin-bottom: 32px;
      #{$parent}_small & {
         flex: 0 0 350px;
         margin: -10px 0 0;
         font-size: 38px;
         color: $color1; } }
   &__sub-title {
      max-width: 350px;
      margin-bottom: 50px;
      font-size: 29px;
      #{$parent}_small & {
         margin-bottom: 30px;
         max-width: none;
         @include f('pt700');
         font-size: 21px; } }

   &__link {
      margin-left: -3px;
      font-size: 17px; }
   &__img-wrap {
      position: absolute;
      top: 0;
      right: 0;
      flex: 0 0 907px; }
   &__text-content {
      flex: 0 0 410px; } }
