// *** begin btn *** //
.btn {
   position: relative;
   display: inline-block;
   padding: 16px 47px;
   @include f('pn700', 18, 28);
   text-align: center;
   transition: .3s ease-in-out;
   overflow: hidden;
   background-color: $color9;
   color: $color1;
   border-radius: 30px;
   outline: 0;
   cursor: pointer; }

// end btn //

// *** begin link *** //
.link {
   $parent: &;
   display: inline-flex;
   align-items: center;
   color: $color1;
   &_bold {
      @include f('pt700'); }
   &_white {
      color: $white; }
   &_decor {
      display: inline;
      span {
         border-bottom: 1px solid currentColor;
         transition: .3s ease-in-out; }
      &:hover {
         span {
            border-bottom-color: transparent; } } }
   &__icon {
      margin-left: 7px;
      #{$parent}:hover & {
         animation: smMoveRight .3s ease-in-out; }
      #{$parent}_bold & {
         margin-left: 11px; }
      #{$parent}_white & {
         fill: $white; } } }
// end link //

// *** begin help-btn *** //
.help-btn {
   @include flexCenter;
   width: 44px;
   height: 44px;
   background-color: #6600cc;
   transition: .3s ease-in-out;
   &:hover {
      background-color: darken(#6600cc, 10); }
   svg {
      fill: $white;
      font-size: 30px; }
   &__text {
      position: absolute;
      left: calc(100% + 55px);
      width: 80px;
      @include f('pt700', 21);
      color: $color9;
      transition: .3s ease-in-out;
      &:hover {
         color: $color1; } } }
// end help-btn //
